.StyledButton2 {
  padding: 1rem 1rem;
  border-radius: 5px;
  background: rgba(0,0,0,.5);
  color: #ffffff;
  font-size: .85rem;
  box-shadow: -0.16em -0.2em red, 0.16em 0.2em rgb(255, 153, 0);
  text-align: center;
  vertical-align: middle;
  font-family: coder;
  transition: .3s ease;
  cursor: pointer;
  transform: scale(1);
  user-select: none;
}  
.StyledButton3 {
  padding: 1rem 1rem;
  border-radius: 5px;
  background: rgba(0,0,0,.5);
  color: #fff;
  font-size: .85rem;
  box-shadow: -0.16em -0.2em red, 0.16em 0.2em rgb(255, 153, 0);
  position: right;
  margin-left: 4px;
  margin-right: 4px;
  text-align: center;
  vertical-align: middle;
  font-family: coder;
  transition: .3s ease;
  cursor: pointer;
  transform: scale(1);
  user-select: none;
}
